@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: local("PT Sans"), local("PTSans-Regular"), url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
}
@font-face {
  font-family: "PT Sans";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: local("PT Sans Bold"), local("PTSans-Bold"), url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
}
img {
  max-width: 100%;
  height: auto;
}
.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  white-space: nowrap;
}
.button {
  margin: 0;
  padding: 15px 47px;
  display: inline-block;
  border: none;
  box-sizing: border-box;
  color: #ffffff;
  text-transform: uppercase;
  text-decoration: none;
  font-family: "PT Sans", "Arial", sans-serif;
  font-size: 21px;
  line-height: 26px;
  font-weight: 700;
  cursor: pointer;
  background-color: #81b3d2;
}
.button:hover {
  background-color: #669ec0;
}
.button:active {
  background-color: #5496bd;
  color: rgba(255, 255, 255, 0.3);
}
.button:disabled,
.button[disabled] {
  background-color: #e5e5e5;
}
.page {
  height: 100%;
  font-size: 14px;
  line-height: 21px;
  font-family: "PT Sans", "Arial", sans-serif;
}
.page__body {
  margin: 0 auto;
  height: 100%;
  min-width: 320px;
  background-color: #f2f2f2;
  text-transform: uppercase;
}
@media (min-width: 768px) {
  .page__body {
    max-width: 1200px;
  }
}
.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  background-color: #ffffff;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.2);
}
.page-header {
  min-height: 56px;
  background-color: #ffffff;
}
.page-header__logo {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  z-index: 3;
}
.page-header__logo--no-js {
  padding-bottom: 5px;
  position: static;
  display: block;
  left: 0;
  transform: translateX(0);
  text-align: center;
}
@media (min-width: 768px) {
  .page-header__logo--no-js {
    padding-bottom: 0;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
  }
}
@media (min-width: 1200px) {
  .logo img {
    width: 187px;
    height: 96px;
