}
.main-nav {
  position: relative;
}
.main-nav__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 3;
  width: 100%;
}
@media (min-width: 768px) {
  .main-nav__list {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    min-height: 56px;
    position: static;
    align-items: center;
  }
}
.main-nav__item {
  border-top: 1px solid #9ac2db;
  border-bottom: 1px solid #678fa8;
}
.main-nav__item:first-child {
  border-top: none;
}
.main-nav__item:last-child {
  position: relative;
  border-bottom: none;
}
.main-nav__item:last-child::after {
  content: "";
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 7px;
  background: linear-gradient(180deg, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
}
@media (min-width: 768px) {
  .main-nav__item {
    padding: 0 20px 0 20px;
    border: none;
    width: 20%;
    box-sizing: border-box;
  }
  .main-nav__item:nth-of-type(4n + 2) {
    margin-right: 20%;
  }
  .main-nav__item:last-child::after {
    display: none;
  }
}
.main-nav__item a[href]:hover {
  background-color: #669ec0;
}
.main-nav__item a[href]:active {
  color: rgba(255, 255, 255, 0.3);
  background-color: #5496bd;
}
@media (min-width: 768px) {
  .main-nav__item a[href]:hover {
    color: #81b3d2;
    background-color: transparent;
  }
  .main-nav__item a[href]:active {
    color: rgba(0, 0, 0, 0.3);
    background-color: transparent;
  }
}
.main-nav__link {
  margin: 0;
  padding: 10px 0 10px 0;
  display: block;
  box-sizing: border-box;
  background-color: #81b3d2;
  color: #ffffff;
  font-size: 18px;
  line-height: 40px;
  font-weight: 700;
  text-align: center;
  text-decoration: none;
}
@media (min-width: 768px) {
  .main-nav__link {
    font-size: 14px;
    line-height: 26px;
    color: #000000;
    background-color: transparent;
  }
}
.main-nav__link--current {
  background-color: #669ec0;
}
@media (min-width: 768px) {
  .main-nav__link--current {
    color: #766357;
    background-color: transparent;
  }
}
.main-nav__toggle {
  padding: 20px;
  position: absolute;
  z-index: 4;
  top: 0;
  right: 0;
  bottom: 0;
  width: 60px;
  height: 57px;
  cursor: pointer;
  border: none;
  background-color: transparent;
  outline: none;
}
.main-nav__toggle:hover,
.main-nav__toggle:active {
  background-color: transparent;
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
.main-nav--closed .main-nav__toggle-lines,
.main-nav--closed .main-nav__toggle-lines::before,
.main-nav--closed .main-nav__toggle-lines::after,
.main-nav--opened .main-nav__toggle-lines::before,
.main-nav--opened .main-nav__toggle-lines::after {
  content: "";
  position: absolute;
  top: 17px;
  left: 20px;
  width: 23px;
  height: 4px;
  background-color: #81b3d2;
}
.main-nav--closed .main-nav__toggle-lines:hover,
.main-nav--closed .main-nav__toggle-lines:hover::before,
.main-nav--closed .main-nav__toggle-lines:hover::after {
  background-color: #b3d1e4;
}
.main-nav--closed .main-nav__toggle-lines:active,
.main-nav--closed .main-nav__toggle-lines:active::before,
.main-nav--closed .main-nav__toggle-lines:active::after {
  background-color: #d9e8f2;
}
.main-nav--closed .main-nav__toggle-lines::after {
  top: 8px;
  left: 0;
}
.main-nav--closed .main-nav__toggle-lines::before {
  top: 16px;
  left: 0;
}
.main-nav--opened .main-nav__toggle-lines::before,
.main-nav--opened .main-nav__toggle-lines::after {
  background-color: #ffffff;
}
.main-nav--opened .main-nav__toggle:hover .main-nav__toggle-lines::before,
.main-nav--opened .main-nav__toggle:hover .main-nav__toggle-lines::after {
  background-color: #cde1ed;
}
.main-nav--opened .main-nav__toggle:active .main-nav__toggle-lines::before,
.main-nav--opened .main-nav__toggle:active .main-nav__toggle-lines::after {
  background-color: #a7cae0;
}
.main-nav--opened .main-nav__toggle-lines {
  background-color: transparent;
}
.main-nav--opened .main-nav__toggle-lines::after {
  top: 28px;
  transform: rotate(45deg);
}
.main-nav--opened .main-nav__toggle-lines::before {
  top: 28px;
  transform: rotate(-45deg);
}
@media (max-width: 767px) {
  .main-nav--closed .main-nav__list {
    display: none;
  }
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
  .main-nav--nojs .main-nav__list {
    position: static;
  }
}
